<template>
    <div class="feedback">
        <top></top>
        <FAlert></FAlert>
        <FConfirm></FConfirm>
        <FLoading></FLoading>
        <div class="container header">
            <img src="./assets/lithi_logo.png" alt="">
            <span style="
            transform: translateY(8px);
            display: inline-block;
            font-size: 24px;
            margin-left: 10px">用户反馈调查</span>
        </div>
        <div class="container notice">
            亲爱的会员，为了给您提供更好的服务，我们希望收集您使用福分通的看法或建议。
            有什么好或不好的地方，请大声说出来！我们会关注您的反馈，不断优化产品，为您提供更好的服务。
            如需了解最新情况请到“我的反馈”中查看处理进度。
            衷心的感谢您的配合和支持！
        </div>
        <div class="container form-table">
            <div class="form">
                <p><span class="red-point">*</span>1、您想对哪方面提出建议？<span class="red-notice" v-if="check.iken">请选择您想对哪方面提出建议</span></p>
                <ul>
                    <li class="menn">
                        <label for="order">
                            <input id="order" type="radio" value="与订单相关" v-model="form.iken" name="type">
                            与订单相关
                        </label>
                    </li>
                    <li class="menn">
                        <label for="shopping">
                            <input id="shopping" type="radio" value="购物问题" v-model="form.iken" name="type">
                            购物问题
                        </label>
                    </li>
                    <li class="menn">
                        <label for="user">
                            <input id="user" type="radio" value="账户问题" v-model="form.iken" name="type">
                            账户问题
                        </label>
                    </li>
                    <li class="menn">
                        <label for="iken">
                            <input id="iken" type="radio" value="优化意见" v-model="form.iken" name="type">
                            优化意见
                        </label>
                    </li>
                    <li class="menn">
                        <label for="other">
                            <input id="other" type="radio" value="其他" v-model="form.iken" name="type">
                            其他
                        </label>
                    </li>
                </ul>
                <p><span class="red-point">*</span>2、我们存在哪些不足？<span class="red-notice" v-if="check.ketten">请填写反馈内容，最少10个字,最多1000字。</span></p>
                <textarea v-model="form.ketten" name="ketten" id="ketten"
              placeholder="请详细描述您遇到的问题或提出的建议，我们会认真对待每一个会员的反馈，持续改进服务。涉及具体页面，请上传相关说明图片（请输入10-1000个字）"></textarea>
                <p class="ar" style="transform: translate(-50px,-50px);">您还可以输入 <span style="color: red">{{1000-form.ketten.length}}</span>字</p>
                <p>
                    <!--<span class="red-point">*</span>-->
                    3、上传说明图片</p>
                <ul>
                    <li class="pic" v-for="(i,key) in form.pic" :key="key">
                        <img :src="i|img_mapi" alt="">
                    </li>
                    <li class="add-pic cursor" v-if="form.pic.length<5">
                        <label class="cursor" for="upload">
                            <input id="upload" @change="upload" :ref="`upload`" type="file" accept="image/*" style="display: none">
                        </label>
                    </li>
                </ul>
                <p><span class="red-point">*</span>4、请填写您企业名称 <span class="red-notice" v-if="check.company">请填写公司名称</span></p>
                <input v-model="form.company" class="company" type="text" placeholder="请填写公司全称">
                <p><span class="red-point">*</span>5、请留下您的联系方式，方便我们及时和您沟通</p>
                <label for="username">
                    您的称呼：
                    <input v-model="form.username" id="username" type="text" placeholder="先生/女士">
                    <span class="red-notice" v-if="check.username">请填写您的称呼</span>
                </label>
                <label for="mobile">
                    您的手机：
                    <input v-model="form.mobile" id="mobile" type="text" placeholder="请填写11位数的手机号码">
                    <span class="red-notice" v-if="check.mobile">请填写11位数的手机号码</span>
                </label>
                <button :class="[{access:true},'cursor']" @click="checkout">提交</button>
            </div>
        </div>
        <footbar></footbar>
    </div>
</template>

<script>
    import top from '../../components/top.vue'
    import footbar from '../../components/foot'
    import axios from 'axios'
    import FAlert from '../../components/FAlert'
    import FConfirm from '../../components/FConfirm'
    import FLoading from '../../components/FLoading'
    export default {
        data() {
            return {
                form:{
                    iken:'',
                    ketten:'',
                    company:'',
                    pic:[],
                    username:'',
                    mobile:''
                },
                check:{
                    iken:false,
                    ketten:false,
                    company:false,
                    username:false,
                    mobile:false
                },
                phonetest:/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/
            }
        },
        mounted(){
            this.bus.$on('confirm-ok',function () {

            }.bind(this))
        },
        watch:{
          form:{
              handler(form){
                  for (let i in this.form){
                      if (i==='pic'){
                          continue
                      }else if(i==='ketten'){
                          if(this.form['ketten'].length > 10&&this.form['ketten'].length<2000){
                              this.check[i] = false
                          }
                      }else if (this.form[i].length!==0){
                          this.check[i] = false
                      }else if(i==='mobile'&&this.phonetest.test(this.form[i])){
                          this.check[i] = false
                      }
                  };
              },
              deep:true
          }
        },
        methods: {
            upload() {
                let file = this.$refs.upload.files[0];
                let formData = new FormData();
                formData.append('image', file);
                axios.post('http://mapi.fufentong.com/www/api/file/uploads', formData).then(res => {
                    if (res.data.status === 200) {
                        this.form.pic.push(res.data.data)
                    }
                })
            },
            load(){
                for (let i in this.check){
                    if (this.check[i]){
                        this.bus.$emit('notice','请填写填写是否有误',1000);
                        return
                    }
                }
                let data = JSON.stringify(this.form);
                this.bus.$emit('loading','正在提交',false);
                this.yum('api/feedback/saveFeedbackInfo',{system:1,infos:data}).then(res=>{
                    setTimeout(function () {
                        this.bus.$emit('loading','上传中',true);
                        this.bus.$emit('notice',res.data.msg,800);
                        if (res.data.status === 200){
                            location.href = '/'
                        }
                    }.bind(this),1000);
                })
            },
            checkout(){
                for (let i in this.form){
                    if (i==='pic'){
                        continue
                    }else if(i==='ketten'){
                        if(this.form['ketten'].length < 10||this.form['ketten'].length>2000){
                            this.check[i] = true
                        }
                    }else if (this.form[i].length===0){
                        this.check[i] = true
                    }else if(i==='mobile'&&!this.phonetest.test(this.form[i])){
                        this.check[i] = true
                    }
                };
                this.load()
            }
        },
        components:{
            top,
            footbar,FConfirm,FAlert,FLoading
        }
    }
</script>

<style scoped lang="stylus">
    .container
        width 1000px
    .header
        height 100px
        line-height 40px
        padding 30px 5px
        border-bottom 2px solid #ff0077
    .notice
        padding 10px
        margin-top 10px
        background-color #e8f4fa
    .form-table
        margin-top 20px
        margin-bottom 80px
        .form
            width 800px
            min-height 777px
            background-color white
            border 1px solid #dcdcdc
            margin 0 auto 0
            padding 20px
            color black
            .red-point
                color: red;transform: scale(1.9) translateY(2px);margin-right: 5px;display: inline-block;
                height 12px
                line-height 12px
            ul
                margin 20px 14px
                position relative
                li.menn
                    display inline-block
                    line-height 16px
                    margin-right 20px
                    input[type=radio]
                        width 16px
                        height 16px
                        vertical-align middle
                    label
                        vertical-align middle
                li.pic
                    position relative
                    display inline-block
                    margin-right 20px
                    width 80px
                    height 80px
                    border 1px solid #dcdcdc
                    border-radius 6px
                    img
                        position absolute
                        width 80px
                        height 80px
                        top 0
                        left 0
                        border-radius 6px
                li.add-pic
                    display inline-block
                    width 80px
                    height 80px
                    background url("./assets/plus.png")
                    label
                        display block
                        height 80px
                        margin 0
            textarea#ketten
                margin 20px 14px
                width 711px
                height 200px
                padding 10px 20px
                border-radius 6px
                border 1px solid #dcdcdc
                outline none
                resize none
                &::-webkit-input-placeholder
                color #dcdcdc
                &::-moz-placeholder
                    color #dcdcdc
                &::-ms-placeholder
                    color #dcdcdc
            input.company
                margin 15px 14px
                padding 15px 17px
                width 330px
                height 40px
                outline none
                border 1px solid #dcdcdc
                border-radius 6px
                &::-webkit-input-placeholder
                    color #dcdcdc
                &::-moz-placeholder
                    color #dcdcdc
                &::-ms-placeholder
                    color #dcdcdc
            label
                display block
                margin-left 14px
                margin-top 10px
                input
                    height 30px
                    padding 0 10px
                    outline none
                    border 1px solid #dcdcdc
                    border-radius 6px
                    &::-webkit-input-placeholder
                        color #dcdcdc
                    &::-moz-placeholder
                        color #dcdcdc
                    &::-ms-placeholder
                        color #dcdcdc
            button
                display block
                margin 30px auto
                width 120px
                height 36px
                outline none
                background-color #dcdcdc
                border none
                color #999
                &.access
                    background-color #ff0077
                    color white
            .red-notice
                margin-left 20px
                color red
</style>
